@charset "utf-8";

*{margin: 0; padding: 0;}
.header{
  background: #00f;
  text-align: center;
}
body{
  font-family: "맑은 고딕",Arial, Helvetica, sans-serif;
  font-size: 16px;
  color: #333;
}
.header > h1{
  color: #fff;
  font-size: 30px;
}
/* 메인서식 */
main{padding: 15px 10px;}
.list01{
  margin-left:10px 0px 15px 30px;
}
.list01 li{
  line-height: 34px;
}
.list01 ul, .list01 li ol{
  margin-left: 16px;
  background: #ececec;
}
main .title2{
  color: #f00;
  line-height: 30px;
  margin: 14px 0px;
}
main .title3{
  color: rgb(177, 68, 199);
  margin: 10px 0px;
}
main .title4{
  line-height: 50px;
}
.footer{
  text-align: center;
  line-height: 40px;
  background: #333;
}
.footer > address{
  color: #fff;
  font-style: normal;
}
