/* /sub_style.css */

.txt_color1{
	color: rgb(234,0,255);
}

#bg_color1{
	background: #ccc;
}

/* 배경 이미지 */
.bg_img{
	width: 342px; height: 228px;

}
/* Count1 서식 */
.btn{
	width: 40px; height: 30px;
	font-size: 20px;
	margin: 10px;
}
.num{
	color: #f00;
	font-weight: bold;
	font-size: 30px;
}
/* Kullyexec 서식 */
.kullyBox{
	width: 500px;
	display: flex;
	justify-content: space-between;
	position: relative;
}
.kullyBox > span{
	position: absolute;
	bottom: 0px; right: 0px;
}
.btn_box{
	border: 1px solid #ccc;
	display: inline-block;
	border-radius: 5px;
	margin: 2px;
	margin-top: 10px;
}
.btn1{
	border: none;
	width: 40px; height: 30px;
	background: none;
	font-size: 26px;
	cursor: pointer;
}
.total{
	transform: translate(17% , 50px);
}
.total > span{
	font-size: 22px;
	font-weight: bold;
}